.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}


.App__wrapper {
  width: 400px;
  margin: 0 auto;
}

.keypad {
  display: block;
  margin: 0 auto;
  position: relative;
  max-width: 400px;
  width: 100%;
  text-align: center;
}

.keypad__numbers {
  display: inline-block;
  width: 80%;
}
.keypad__operators {
  display: inline-block;
  width: 20%;
}


@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}