body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.keypad__button {
    border: none;
    color: black;
    cursor: pointer;    
    font-size: 2rem;
    padding: 20px 30px;
    position: relative;
    width: 100%;
    -webkit-transition: all 0.3s;
    transition: all 0.3s;
}
.keypad__button:active {
    top: 1px;
    background: #00000032;
    -webkit-transition: all 0.3s;
    transition: all 0.3s;
}
.keypad__button--numbers {
    background: rgba(255,255,255,0.1);
    border-radius: 2px;
    color: #fff;
}

.keypad__button--operators {
    background: none;
    color: #fff;
    padding: 0;
}

.keypad__button-wrapper {
    display: inline-block;
    width: 30%;
    padding: 5px;
    text-align: center
}

.keypad__button-wrapper--operators {
    display: block;
    width: 100%;
}


.display__wrapper {
    background: #00000032;
    border-radius: 3px;
    display: table;
    height: 80px;
    margin-bottom: 10px;
    width: 100%;
}

.display__input, .display__text {
    background: none;
    border-width: 0;
    color: orange;
    display: table-cell;
    font-size: 2rem;
    font-weight: 600;
    overflow: hidden;
    padding: 0 30px;
    text-align: left;
    width: 100%;
    vertical-align: middle;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}


.App__wrapper {
  width: 400px;
  margin: 0 auto;
}

.keypad {
  display: block;
  margin: 0 auto;
  position: relative;
  max-width: 400px;
  width: 100%;
  text-align: center;
}

.keypad__numbers {
  display: inline-block;
  width: 80%;
}
.keypad__operators {
  display: inline-block;
  width: 20%;
}


@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}


@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
